@keyframes shake {
  0% {
    padding-left: 0;
  }
  50% {
    padding-left: 10px;
  }
  100% {
    padding-left: 0px;
  }
}

@keyframes shakeTR {
  0% {
    top: 10px;
    right: 10px;
  }
  50% {
    top: 5px;
    right: 5px;
  }
  100% {
    top: 10px;
    right: 10px;
  }
}

@keyframes shakeTD {
  0% {
    margin-top: 0px;
  }
  33% {
    margin-top: -10px;
  }
  66% {
    margin-top: 10px;
  }
  100% {
    margin-top: 0px;
  }
}

.moveTDOnHover {
  margin-top: 0;
}
.moveTDOnHover:hover {
  animation: shakeTD 0.5s;
}

.moveLROnHover,
.moveLROnClick {
  padding-left: 0;
}
.moveLROnHover:hover,
.moveLROnClick:active {
  animation: shake 0.5s;
}

.moveTopRightOnHover img {
  top: 10px;
  right: 10px;
}

.moveTopRightOnHover:hover img {
  animation: shakeTR 0.5s;
}

.rotateOnHover {
  transition: transform 0.2s;
  transform: rotateY(0deg);
}

.rotateOnHover:hover {
  transform: rotateY(30deg);
}

.rotateOnHover:active {
  transform: rotateY(180deg);
}

.heightTransition {
  max-height: 0px;
  transition: max-height 1s;
}
